"use client";

import { z } from "zod";
import { zodResolver } from "@hookform/resolvers/zod";
import { useForm } from "react-hook-form";

import { useEmailInput } from "@/core/shared/hooks/useEmailInput";
import { useTranslation } from "@/core/shared/i18n/client";
import { Support } from "../../components/Support/Support";
import css from "./StepRegister.module.css";

export interface StepRegisterProps {
  onRegister: (email: string, password: string) => void;
  onEmailChange: (email: string) => void;
  email: string;
  isLoading?: boolean;
  errorMessage?: string;
}

interface FormData {
  email: string;
  password: string;
  confirmPassword: string;
}

const registerSchema = z.object({
  email: z.string().email("Please enter a valid email address"),
  password: z.string().min(6, "Password must be at least 6 characters"),
  confirmPassword: z.string(),
}).refine((data) => data.password === data.confirmPassword, {
  message: "Passwords don't match",
  path: ["confirmPassword"],
});

export const StepRegister = ({
  onRegister,
  onEmailChange,
  email,
  isLoading = false,
  errorMessage,
}: StepRegisterProps) => {
  const { t } = useTranslation(["auth"]);

  const {
    register,
    handleSubmit,
    formState: { errors },
    setValue,
  } = useForm<FormData>({
    resolver: zodResolver(registerSchema),
    defaultValues: { email, password: "", confirmPassword: "" },
  });

  const { handleInputChange } = useEmailInput({
    onEmailChange: (newEmail) => {
      setValue("email", newEmail);
      onEmailChange(newEmail);
    },
  });

  const onSubmit = (data: FormData) => {
    if (isLoading) return;
    onRegister(data.email, data.password);
  };

  return (
    <div>
      <h2>{t("signUp")}</h2>
      
      <form onSubmit={handleSubmit(onSubmit)}>
        <div>
          <label htmlFor="email">{t("email")}</label>
          <input
            type="email"
            id="email"
            {...register("email")}
            onChange={handleInputChange}
            placeholder={t("enterEmail")}
          />
          {errors.email && <span className="error">{errors.email.message}</span>}
        </div>

        <div>
          <label htmlFor="password">{t("password")}</label>
          <input
            type="password"
            id="password"
            {...register("password")}
            placeholder={t("enterPassword")}
          />
          {errors.password && <span className="error">{errors.password.message}</span>}
        </div>

        <div>
          <label htmlFor="confirmPassword">{t("confirmPassword")}</label>
          <input
            type="password"
            id="confirmPassword"
            {...register("confirmPassword")}
            placeholder={t("confirmPassword")}
          />
          {errors.confirmPassword && <span className="error">{errors.confirmPassword.message}</span>}
        </div>

        {errorMessage && (
          <span className="error">{errorMessage}</span>
        )}

        <button type="submit" disabled={isLoading}>
          {isLoading ? t("loading") : t("signUp")}
        </button>

        <Support />
      </form>
    </div>
  );
};
