"use client";

import { z } from "zod";
import { zodResolver } from "@hookform/resolvers/zod";
import { useForm } from "react-hook-form";

import { useEmailInput } from "@/core/shared/hooks/useEmailInput";
import { useTranslation } from "@/core/shared/i18n/client";
import { Support } from "../../components/Support/Support";
import css from "./StepLogin.module.css";

export interface StepLoginProps {
  onLogin: (email: string, password: string) => void;
  onEmailChange: (email: string) => void;
  email: string;
  isLoading?: boolean;
  errorMessage?: string;
}

interface FormData {
  email: string;
  password: string;
}

const loginSchema = z.object({
  email: z.string().email("Please enter a valid email address"),
  password: z.string().min(6, "Password must be at least 6 characters"),
});

export const StepLogin = ({
  onLogin,
  onEmailChange,
  email,
  isLoading = false,
  errorMessage,
}: StepLoginProps) => {
  const { t } = useTranslation(["auth"]);

  const {
    register,
    handleSubmit,
    formState: { errors },
    setValue,
  } = useForm<FormData>({
    resolver: zodResolver(loginSchema),
    defaultValues: { email, password: "" },
  });

  const { handleInputChange } = useEmailInput({
    onEmailChange: (newEmail) => {
      setValue("email", newEmail);
      onEmailChange(newEmail);
    },
  });

  const onSubmit = (data: FormData) => {
    if (isLoading) return;
    onLogin(data.email, data.password);
  };

  return (
    <div>
      <h2>{t("signIn")}</h2>
      
      <form onSubmit={handleSubmit(onSubmit)}>
        <div>
          <label htmlFor="email">{t("email")}</label>
          <input
            type="email"
            id="email"
            {...register("email")}
            onChange={handleInputChange}
            placeholder={t("enterEmail")}
          />
          {errors.email && <span className={css.error}>{errors.email.message}</span>}
        </div>

        <div>
          <label htmlFor="password">{t("password")}</label>
          <input
            type="password"
            id="password"
            {...register("password")}
            placeholder={t("enterPassword")}
          />
          {errors.password && <span className={css.error}>{errors.password.message}</span>}
        </div>

        {errorMessage && (
          <span className={css.error}>{errorMessage}</span>
        )}

        <button type="submit" disabled={isLoading}>
          {isLoading ? t("loading") : t("signIn")}
        </button>

        <Support />
      </form>
    </div>
  );
};
