"use client";

import { routes } from "@/core/shared/router";
import { useRouter } from "next/navigation";
import { useCallback, useEffect, useMemo } from "react";
import { AuthFlowStep } from "../../model/constants/flowSteps";
import { useAuthFlow } from "./useAuthFlow";
import { StepAuthType } from "../steps/StepAuthType/StepAuthType";
import { StepLogin } from "../steps/StepLogin/StepLogin";
import { StepRegister } from "../steps/StepRegister/StepRegister";
import { StepEnterEmail } from "../steps/StepEnterEmail/StepEnterEmail";
import { useLogin } from "../../hooks/useLogin";
import { useRegister } from "../../hooks/useRegister";

// import css from "./AuthFlow.module.css";

interface AuthFlowProps {
  onStepChange?: (step: AuthFlowStep) => void;
}

export const AuthFlow = ({ onStepChange }: AuthFlowProps) => {
  const router = useRouter();

  const onAuthSuccess = useCallback(() => {
    router.push(routes.index);
  }, [router]);

  const {
    currentStep,
    email,
    authType,
    handleResetToAuthTypeStep,
    handleSetCurrentEmail,
    handleAuthTypeSelect,
    handleStepEnterEmailComplete,
    handleStepFinishSignUpComplete,
  } = useAuthFlow({ onSuccess: onAuthSuccess });

  const { login, isLoading: isLoginLoading, hasError: hasLoginError, errorMessage: loginErrorMessage } = useLogin();
  const { register, isLoading: isRegisterLoading, hasError: hasRegisterError, errorMessage: registerErrorMessage } = useRegister();

  useEffect(() => {
    onStepChange?.(currentStep);
  }, [currentStep, onStepChange]);

  const handleLogin = useCallback(async (email: string, password: string) => {
    await login(email, password);
    // If login is successful, onAuthSuccess will be called
  }, [login]);

  const handleRegister = useCallback(async (email: string, password: string) => {
    await register(email, password);
    // If registration is successful, onAuthSuccess will be called
  }, [register]);

  const authStep = useMemo(() => {
    switch (currentStep) {
      case AuthFlowStep.AuthType:
        return (
          <StepAuthType
            onAuthTypeSelect={handleAuthTypeSelect}
          />
        );
      case AuthFlowStep.Login:
        return (
          <StepLogin
            email={email}
            onEmailChange={handleSetCurrentEmail}
            onLogin={handleLogin}
            isLoading={isLoginLoading}
            errorMessage={hasLoginError ? loginErrorMessage : undefined}
          />
        );
      case AuthFlowStep.Register:
        return (
          <StepRegister
            email={email}
            onEmailChange={handleSetCurrentEmail}
            onRegister={handleRegister}
            isLoading={isRegisterLoading}
            errorMessage={hasRegisterError ? registerErrorMessage : undefined}
          />
        );
      case AuthFlowStep.EnterEmail:
        return (
          <StepEnterEmail
            email={email}
            onEmailChange={handleSetCurrentEmail}
            onComplete={handleStepEnterEmailComplete}
          />
        );
      default:
        return null;
    }
  }, [currentStep, email, authType, handleAuthTypeSelect, handleSetCurrentEmail, handleLogin, handleRegister, handleStepEnterEmailComplete, isLoginLoading, isRegisterLoading, hasLoginError, hasRegisterError, loginErrorMessage, registerErrorMessage]);

  return <div>{authStep}</div>;
};
