"use client";

import { useTranslation } from "@/core/shared/i18n/client";
import css from "./StepAuthType.module.css";

export enum AuthType {
  Login = "login",
  Register = "register",
}

export interface StepAuthTypeProps {
  onAuthTypeSelect: (authType: AuthType) => void;
}

export const StepAuthType = ({ onAuthTypeSelect }: StepAuthTypeProps) => {
  const { t } = useTranslation(["auth"]);

  return (
    <div>
      <h2>{t("welcome")}</h2>
      <p>{t("chooseAuthType")}</p>
      
      <div>
        <button 
          type="button" 
          onClick={() => onAuthTypeSelect(AuthType.Login)}
          className={css["auth-type-button"]}
        >
          {t("signIn")}
        </button>
        
        <button 
          type="button" 
          onClick={() => onAuthTypeSelect(AuthType.Register)}
          className={css["auth-type-button"]}
        >
          {t("signUp")}
        </button>
      </div>
    </div>
  );
};
